.library {
    padding: 30px;
}

.block {
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.heading {
    margin-top: 0;
}

.item {
    width: 49%;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    padding-right: 5px;
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
}

.icon {
    width: 128px;
    text-align: center;
    font-size: 50px;
    margin: 30px 0;
}

.thumbnail {
    width: 128px;
}

.title {
    margin: 0;
    padding: 60px 0 30px 30px;
    font-size: 18px;
}

.authors {
    display: block;
}

.link {
    color: #3399ff;
    position: absolute;
    top: 0;
    right: 10px;
}

.bookmark {
    font-size: 50px;
}

.overlay {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .8);
}

.button {
    height: 60px;
    width: 60px;
    color: #fff;
    position: absolute;
    top: 20px;
    right: 40px;
    z-index: 10;
    background: #000;
    display: grid;
    place-items: center;
    text-decoration: none;
}

.cancel {
    font-size: 60px;
    height: 100%;
}

@media screen and (max-width: 900px) {
    .item {
        width: 100%;
    }
}