.subscribe {
    padding: 50px 0;
}

.flexBlock {
    display: flex;
}

.img {
    width: 50%;
    padding: 0 30px;
    box-sizing: border-box;
    background: url(../../assets/images/newsletter_bg.jpg) 0 0 no-repeat;
    background-size: cover;
    position: relative;
    z-index: -1;
    display: flex;
    align-items: center;
}

.img::before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.heading {
    color: #fff;
    font-size: 24px;
}

.form {
    width: 50%;
    padding: 50px 30px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.label {
    margin-bottom: 15px;
    font-size: 20px;
}

.input {
    font-size: 16px;
    outline: none;
    -webkit-appearance: none;
    -webkit-border: none;
    border: none;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    border-bottom: 3px solid #3399ff;
    box-sizing: border-box;
    width: 100%;
    padding: 15px 15px 10px 15px;
    color: #000;
    font-size: 20px;
    z-index: 1;
    margin-bottom: 20px;
}

.input:focus::placeholder {
    color: #fff;
}

.button {
    display: inline-block;
    font-size: 16px;
    text-align: center;
    border: none;
    background: #3399ff;
    color: #fff;
    outline: none;
    padding: 15px 0;
    cursor: pointer;
    border-radius: 6px;
}

@media screen and (max-width: 700px) {
    .flexBlock {
        flex-direction: column;
    }
    .img {
        width: 100%;
    }
    .form {
        width: 100%;
    }
}