.main {
    display: flex;
}

.interface {
    width: calc(100% - 70px);
}

.scene {
    box-sizing: border-box;
    width: 100%;
    height: calc(100vh - 70px);
    overflow: scroll;
}

@media screen and (max-width: 500px) {
    .main {
        flex-direction: column-reverse;
    }
    .interface {
        width: 100%;
        height: calc(100vh - 70px);
    }
    .scene {
        height: calc(100vh - 140px);
    }
}