.notes {
    padding: 30px;
}

.heading {
    margin-top: 0;
}

.block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.field {
    font-size: 18px;
    padding: 20px;
    width: 49%;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    box-sizing: border-box;
    border-radius: 6px;
}

.textarea {
    font-size: 18px;
    padding: 20px;
    width: 49%;
    height: 300px;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    box-sizing: border-box;
    -webkit-border-radius: 6px;
    border-radius: 6px;
    border: 1px solid #ccc;
    outline: none;
    -webkit-appearance: none;
    -webkit-border: none;
    z-index: 1;
}

.field > p {
    margin: 0;
}

.save {
    padding: 30px 0 0 0;
    display: flex;
    justify-content: center;
}

.link {
    display: inline-block;
    background: #3399ff;
    border-radius: 6px;
    padding: 15px 20px;
    text-decoration: none;
    color: #fff;
}

@media screen and (max-width: 500px) {
    .field {
        width: 100%;
        height: 30vh;
    }
    .textarea {
        width: 100%;
        height: 30vh;
    }
}