.center {
    max-width: 500px;
    margin: 0 auto;
}

.profile {
    padding: 30px;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    box-sizing: border-box;
    border-radius: 6px;
}

.heading {
    margin-top: 0;
    text-align: center;
}

.heading > a {
    color: #000;
    margin-left: 10px;
}

.item {
    margin-bottom: 30px;
}

.title {
    margin-top: 0;
}

.text {
    font-size: 18px;
    margin: 0;
    padding: 10px;
    border-bottom: 3px solid #3399ff;
}

.warn {
    float: left;
    color: #999;
}

@keyframes loading {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading {
    display: block;
    margin: 30px auto 0 auto;
    border: 10px solid #ccc;
    border-top: 10px solid #3399ff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: loading .5s linear infinite;
    cursor: default;
}