.search {
    padding: 30px;
}

.heading {
    margin-top: 0;
}

.find {
    position: relative;
}

.input {
    font-size: 16px;
    outline: none;
    -webkit-appearance: none;
    -webkit-border: none;
    border: none;
    -webkit-border-radius: 0px;
    border-radius: 0px;
    border-bottom: 3px solid #3399ff;
    box-sizing: border-box;
    width: 100%;
    padding: 15px 15px 10px 15px;
    color: #000;
    font-size: 20px;
    z-index: 1;
}

.input::placeholder {
    color: #000;
}

.input:focus::placeholder {
    color: #fff;
}

.icon {
    position: absolute;
    top: 15px;
    right: 15px;
}

@keyframes loading {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading {
    display: block;
    margin: 30px auto 0 auto;
    border: 10px solid #ccc;
    border-top: 10px solid #3399ff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: loading .5s linear infinite;
    cursor: default;
}