.about {
    height: 100vh;
    padding: 50px 0;
    box-sizing: border-box;
    display: grid;
    place-items: center;
}

.heading {
    font-size: 24px;
    color: #3399ff;
    margin-top: 0;
}

.text {
    line-height: 2;
    font-size: 18px;
    margin-bottom: 60px;
}

.block {
    display: flex;
    justify-content: space-between;
}

.item {
    width: 30%;
    display: grid;
    place-items: center;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    border-radius: 6px;
    background: #3399ff;
    padding: 40px 0;
    box-sizing: border-box;
    transition: all .3s;
}

.icon {
    font-size: 80px;
    color: #fff;
}

.title {
    font-size: 18px;
    color: #fff;
}

@media screen and (max-width: 700px) {
    .about {
        height: 100%;
    }
    .block {
        flex-direction: column;
    }
    .item {
        width: 100%;
        margin-bottom: 30px;
    }
}