.block {
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.item {
    width: 49%;
    box-shadow: 0px 10px 10px rgba(77, 160, 248, 0.2);
    padding-right: 5px;
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    position: relative;
}

.icon {
    width: 128px;
    text-align: center;
    font-size: 50px;
    margin: 30px 0;
}

.title {
    margin: 0;
    padding: 60px 0 30px 30px;
    font-size: 18px;
}

.authors {
    display: block;
}

.link {
    color: #000;
    position: absolute;
    top: 0;
    right: 10px;
}

.bookmark {
    font-size: 50px;
}

@media screen and (max-width: 900px) {
    .item {
        width: 100%;
    }
}