.heading {
    padding: 30px;
    margin: 0;
}

.heading > a {
    color: #000;
    margin-left: 10px;
}

.datetime {
    width: 45%;
}

@keyframes loading {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.loading {
    display: block;
    margin: 30px auto 0 auto;
    border: 10px solid #ccc;
    border-top: 10px solid #3399ff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: loading .5s linear infinite;
    cursor: default;
}